import { Component, OnInit, ViewChildren, QueryList, OnDestroy } from '@angular/core';
import { NgModule, ViewChild } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
//for datatable starts

import { DataTableDirective } from "angular-datatables";
//from the particular local folder starts
//import { LrDispatchBookingReportService } from './lr-dispatch-booking-report-service';
//from the particular local folder ends
//from the particular global folder starts
//import { SuplierService } from './supplier-master-service';
import { DashboardService } from 'src/app/dataService/dashboard-service';
//from the particular global folder ends
import { ElementRef } from "@angular/core";
import { Subject, Subscription } from 'rxjs';

//for datatable ends
//for modal starts
//import { OnDestroy } from '@angular/core';
import { MatDialog } from '@angular/material';
import { ActivatedRoute, Router } from '@angular/router';
import { LrDetailsComponent } from "src/app/dashboards/dashboards-popup/lr-details/lr-details.component";
//import {Subscription} from 'rxjs';
//for modal ends
//for the select option with filter starts
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
//for the select option with filter ends
//for popup modal starts 
import { NgbModal, ModalDismissReasons, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
//for popup modal ends
@Component( {
    selector: 'app-delayed-trips',
    templateUrl: './delayed-trips.component.html',
    styleUrls: ['./delayed-trips.component.css']
} )
export class DelayedTripsComponent implements OnDestroy, OnInit {
    //for popup modal starts
    closeResultMobile: string;
    //for popup modal ends
    //for modal starts
    animal: string;
    name: string;
    routeQueryParams$: Subscription;
    //for modal ends

    getDataDelayedTripsDataTable: any;

    delayedTripsDataList: any;

    onDestroyUnsubscribtionDelayedTrips: Subscription;
    @ViewChildren( DataTableDirective ) public dtElements: QueryList<DataTableDirective>;

    dtTriggerDelayedTrips: Subject<any> = new Subject();

    dataTable: any;

    dtOptionsDelayedTrips: any;
    control = new FormControl();
    destinationOptions = [
        { id: 1, label: 'ALL' },
        { id: 2, label: 'Alleppey' },
        { id: 3, label: 'Alwaye' },
        { id: 3, label: 'Bangalore' }
    ];

    constructor(
        private delayedTripsScreen: DashboardService,

        //for modal starts
        public dialog: MatDialog,
        private route: ActivatedRoute,
        private router: Router,
        //for modal ends
        //for popup modal starts
        private modalService: NgbModal
        //for popup modal ends        
    ) {
        //for modal starts
        this.routeQueryParams$ = route.queryParams.subscribe( params => {
            if ( params['dialog'] ) {
                this.openDialog();
            }
        } );
        //for modal ends
    }


    ngOnInit(): void {
        //        the one datatable starts
        this.dtOptionsDelayedTrips = {

            // the below code is for button export starts
            dom: 'Bfrtip',
            /*buttons: [
                      'excel', 'print'
                  ],*/
            buttons: [
                //                {
                //                    extend: 'excel',
                //                    text: '<i class="fas fa-file-excel"> Excel</i>',
                //                    titleAttr: 'Excel',
                //                    exportOptions: {
                //                        columns: ':visible'
                //                    }
                //                },
                //                {
                //                    extend: 'print',
                //                    text: '<i class="fas fa-print"> Print</i>',
                //                    titleAttr: 'Print',
                //                    //                                exportOptions: {
                //                    //                                    columns: ':visible'
                //                    //                                }
                //                }
            ],
            // the below code is for button export ends
            //place holder in search/filter in datatable starts
            language: {
                search: "_INPUT_",
                searchPlaceholder: "Search..."
            },
            //place holder in search/filter in datatable ends
            //pagination in datatable starts
            pagingType: 'full_numbers',
            pageLength: 5,
            //pagination in datatable ends
            processing: true,
            //scroll in datatable starts
            "scrollX": true,
            "scrollCollapse": true,
            //scroll in datatable ends
        }
        //      the one datatable ends

        //one service starts 
        this.getDataDelayedTripsDataTable = this.delayedTripsScreen.getSupplierMasterData()
        this.onDestroyUnsubscribtionDelayedTrips = this.getDataDelayedTripsDataTable.subscribe( data => {
            this.delayedTripsDataList = data['data'];
            this.dtTriggerDelayedTrips.next();
        } );
        //one service ends
    }

    ngOnDestroy(): void {
        this.dtTriggerDelayedTrips.unsubscribe();

        this.onDestroyUnsubscribtionDelayedTrips.unsubscribe();

        //for modal starts
        this.routeQueryParams$.unsubscribe();
        //for modal ends
    }

    //for modal starts
    openDialog(): void {
        const dialogRef = this.dialog.open( LrDetailsComponent, {

            data: { name: this.name, animal: this.animal }
        } );

        dialogRef.afterClosed().subscribe( result => {
            console.log( 'The dialog was closed' );
            this.animal = result;
            this.router.navigate( ['.'], { relativeTo: this.route } );
        } );
    }
    //for modal ends
    //for popup modal starts 
    openDestination( contentDestination ) {
        this.modalService.open( contentDestination, { centered: true } ).result.then(
            resultMobile => {
                this.closeResultMobile = `Closed with: ${resultMobile}`;
            },
            reason => {
                this.closeResultMobile = `Dismissed ${this.getDismissReason( reason )}`;
            }
        );
    }
    private getDismissReason( reason: any ): string {
        if ( reason === ModalDismissReasons.ESC ) {
            return 'by pressing ESC';
        } else if ( reason === ModalDismissReasons.BACKDROP_CLICK ) {
            return 'by clicking on a backdrop';
        } else {
            return `with: ${reason}`;
        }
    }

    //for popup modal ends
    //for the select option with filter starts

    transform( items: any[], searchTerm: string, labelKey?: string ): any {
        if ( !items || !searchTerm ) {
            return items;
        }

        return items.filter(
            item =>
                item[labelKey || 'label']
                    .toLowerCase()
                    .includes( searchTerm.toLowerCase() ) === true
        );
    }

    //for the select option with filter ends
}
