import { ElementRef } from "@angular/core";
import { Subject, Subscription } from 'rxjs';

//for datatable ends

@Component( {
    selector: 'app-scheduled-trips-from',
    templateUrl: './scheduled-trips-from.component.html',
    styleUrls: ['./scheduled-trips-from.component.css']
} )
export class ScheduledTripsFromComponent implements OnDestroy, OnInit {
    getDataScheduledTripsFromDataTable: any;

    scheduledTripsFromDataList: any;

    onDestroyUnsubscribtionScheduledTripsFrom: Subscription;

    @ViewChildren( DataTableDirective ) public dtElements: QueryList<DataTableDirective>;

    dtTriggerScheduledTripsFrom: Subject<any> = new Subject();

    dataTable: any;


    dtOptionsScheduledTripsFrom: any;
    constructor( private scheduledTripsFromScreen: DashboardService ) {


    }

    ngOnInit(): void {
        //      the three datatable starts
        this.dtOptionsScheduledTripsFrom = {
            //};
            //columns is used to for export and others starts
            columns: [

                {
                    title: 'Hireslip Number',
                    data: 'name'
                },
                {
                    title: 'Destination',
                    data: 'symbol'
                },
                {
                    title: 'Departure At',
                    data: 'grade'
                }
            ],

            // the below code is for button export starts
            dom: 'Bfrtip',
            /*buttons: [
                      'excel', 'print'
                  ],*/
            buttons: [

            ],
            // the below code is for button export ends
            //place holder in search/filter in datatable starts
            language: {
                search: "_INPUT_",
                searchPlaceholder: "Search..."
            },
            //for search/filter in datatable to hide starts
            searching: false,
            //for search/filter in datatable to hide ends

            //place holder in search/filter in datatable ends
            //pagination in datatable starts
            pagingType: 'full_numbers',
            pageLength: 7,
            //pagination in datatable ends
            processing: true,
            //scroll in datatable starts
            "scrollX": true,
            "scrollCollapse": true,
            //scroll in datatable ends
        }
        //      the three datatable ends



        //three service starts 
        this.getDataScheduledTripsFromDataTable = this.scheduledTripsFromScreen.getSupplierMasterData()
        this.onDestroyUnsubscribtionScheduledTripsFrom = this.getDataScheduledTripsFromDataTable.subscribe( data => {
            this.scheduledTripsFromDataList = data['data'];
            this.dtTriggerScheduledTripsFrom.next();
        } );
        //three service ends
    }


    ngOnDestroy(): void {
        this.dtTriggerScheduledTripsFrom.unsubscribe();
        this.onDestroyUnsubscribtionScheduledTripsFrom.unsubscribe();
    }

}
