//for popup modal starts 
import { NgbModal, ModalDismissReasons, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
//for popup modal ends
@Component( {
    selector: 'app-final-notice-pending-email',
    templateUrl: './final-notice-pending-email.component.html',
    styleUrls: ['./final-notice-pending-email.component.css']
} )
export class FinalNoticePendingEmailComponent implements OnDestroy, OnInit {

    //for popup modal starts
    closeResultPrint: string;
    closeResultEmail: string;
    closeResultPrintAndEmail: string;
    //for popup modal ends

    getDataFinalNoticePendingEmailDataTable: any;

    finalNoticePendingEmailDataList: any;

    onDestroyUnsubscribtionFinalNoticePendingEmail: Subscription;

    @ViewChildren( DataTableDirective ) public dtElements: QueryList<DataTableDirective>;

    dtTriggerFinalNoticePendingEmail: Subject<any> = new Subject();

    dataTable: any;

    dtOptionsFinalNoticePendingEmail: any;

    constructor( private finalNoticePendingEmailScreen: DashboardService, private modalService: NgbModal ) {


    }

    ngOnInit(): void {

        //      the fifteen datatable starts
        this.dtOptionsFinalNoticePendingEmail = {
            //};
            //columns is used to for export and others starts


            // the below code is for button export starts
            dom: 'Bfrtip',
            /*buttons: [
                      'excel', 'print'
                  ],*/
            buttons: [
                //                {
                //                    extend: 'excel',
                //                    text: '<i class="fas fa-file-excel"> Excel</i>',
                //                    titleAttr: 'Excel',
                //                    exportOptions: {
                //                        columns: ':visible'
                //                    }
                //                },
                //                {
                //                    extend: 'print',
                //                    text: '<i class="fas fa-print"> Print</i>',
                //                    titleAttr: 'Print',
                //                    //                                exportOptions: {
                //                    //                                    columns: ':visible'
                //                    //                                }
                //                }
            ],
            // the below code is for button export ends
            //place holder in search/filter in datatable starts
            language: {
                search: "_INPUT_",
                searchPlaceholder: "Search..."
            },
            //for search/filter in datatable to hide starts
            searching: false,
            //for search/filter in datatable to hide ends
            //place holder in search/filter in datatable ends
            //pagination in datatable starts
            pagingType: 'full_numbers',
            pageLength: 6,
            //pagination in datatable ends
            processing: true,
            //scroll in datatable starts
            "scrollX": true,
            "scrollCollapse": true,
            //scroll in datatable ends
        }
        //      the fifteen datatable ends
        //fifteen service starts 
        this.getDataFinalNoticePendingEmailDataTable = this.finalNoticePendingEmailScreen.getSupplierMasterData()
        this.onDestroyUnsubscribtionFinalNoticePendingEmail = this.getDataFinalNoticePendingEmailDataTable.subscribe( data => {
            this.finalNoticePendingEmailDataList = data['data'];
            this.dtTriggerFinalNoticePendingEmail.next();
        } );
        //fifteen service ends
    }

    ngOnDestroy(): void {
        this.dtTriggerFinalNoticePendingEmail.unsubscribe();

        this.onDestroyUnsubscribtionFinalNoticePendingEmail.unsubscribe();
    }

    //for popup modal starts 
    openModalPrint( contentPrint ) {
        this.modalService.open( contentPrint, { centered: true } ).result.then(
            resultPrint => {
                this.closeResultPrint = `Closed with: ${resultPrint}`;
            },
            reason => {
                this.closeResultPrint = `Dismissed ${this.getDismissReason( reason )}`;
            }
        );
    }

    openModalEmail( contentEmail ) {
        this.modalService.open( contentEmail, { centered: true } ).result.then(
            resultEmail => {
                this.closeResultEmail = `Closed with: ${resultEmail}`;
            },
            reason => {
                this.closeResultEmail = `Dismissed ${this.getDismissReason( reason )}`;
            }
        );
    }

    openModalPrintAndEmail( contentPrintAndEmail ) {
        this.modalService.open( contentPrintAndEmail, { centered: true } ).result.then(
            resultPrintAndEmail => {
                this.closeResultPrintAndEmail = `Closed with: ${resultPrintAndEmail}`;
            },
            reason => {
                this.closeResultPrintAndEmail = `Dismissed ${this.getDismissReason( reason )}`;
            }
        );
    }
    private getDismissReason( reason: any ): string {
        if ( reason === ModalDismissReasons.ESC ) {
            return 'by pressing ESC';
        } else if ( reason === ModalDismissReasons.BACKDROP_CLICK ) {
            return 'by clicking on a backdrop';
        } else {
            return `with: ${reason}`;
        }
    }

    //for popup modal ends

}
