@Component( {
    selector: 'app-scheduled-trips-to',
    templateUrl: './scheduled-trips-to.component.html',
    styleUrls: ['./scheduled-trips-to.component.css']
} )
export class ScheduledTripsToComponent implements OnDestroy, OnInit {

    getDataScheduledTripsToDataTable: any;

    scheduledTripsToDataList: any;

    onDestroyUnsubscribtionScheduledTripsTo: Subscription;

    @ViewChildren( DataTableDirective ) public dtElements: QueryList<DataTableDirective>;

    dtTriggerScheduledTripsTo: Subject<any> = new Subject();

    dataTable: any;

    dtOptionsScheduledTripsTo: any;

    constructor( private scheduledTripsToScreen: DashboardService ) {


    }

    ngOnInit(): void {
        //    the six datatable starts
        this.dtOptionsScheduledTripsTo = {
            //};
            //columns is used to for export and others starts
            columns: [

                {
                    title: 'Hireslip Number',
                    data: 'name'
                },
                {
                    title: 'Destination',
                    data: 'symbol'
                },
                {
                    title: 'Departure At',
                    data: 'grade'
                }
            ],

            // the below code is for button export starts
            dom: 'Bfrtip',
            /*buttons: [
                      'excel', 'print'
                  ],*/
            buttons: [

            ],
            // the below code is for button export ends
            //place holder in search/filter in datatable starts
            language: {
                search: "_INPUT_",
                searchPlaceholder: "Search..."
            },
            //for search/filter in datatable to hide starts
            searching: false,
            //for search/filter in datatable to hide ends
            //place holder in search/filter in datatable ends
            //pagination in datatable starts
            pagingType: 'full_numbers',
            pageLength: 7,
            //pagination in datatable ends
            processing: true,
            //scroll in datatable starts
            "scrollX": true,
            "scrollCollapse": true,
            //scroll in datatable ends
        }
        //      the six datatable ends

        //six service starts 
        this.getDataScheduledTripsToDataTable = this.scheduledTripsToScreen.getSupplierMasterData()
        this.onDestroyUnsubscribtionScheduledTripsTo = this.getDataScheduledTripsToDataTable.subscribe( data => {
            this.scheduledTripsToDataList = data['data'];
            this.dtTriggerScheduledTripsTo.next();
        } );
        //six service ends
    }

    ngOnDestroy(): void {
        this.dtTriggerScheduledTripsTo.unsubscribe();

        this.onDestroyUnsubscribtionScheduledTripsTo.unsubscribe();
    }

}
