import { MatDialog } from '@angular/material';
import { ActivatedRoute, Router } from '@angular/router';
import { EditConsigneeDetailsComponent } from "src/app/dashboards/dashboards-popup/edit-consignee-details/edit-consignee-details.component";
//import {Subscription} from 'rxjs';
//for modal ends
@Component( {
    selector: 'app-pending-consignee',
    templateUrl: './pending-consignee.component.html',
    styleUrls: ['./pending-consignee.component.css']
} )
export class PendingConsigneeComponent implements OnDestroy, OnInit {

    getDataPendingConsigneeDataTable: any;

    pendingConsigneeDataList: any;

    onDestroyUnsubscribtionPendingConsignee: Subscription;

    @ViewChildren( DataTableDirective ) public dtElements: QueryList<DataTableDirective>;

    dtTriggerPendingConsignee: Subject<any> = new Subject();

    dataTable: any;

    dtOptionsPendingConsignee: any;

    //for modal starts
    animal: string;
    name: string;
    routeQueryParams$: Subscription;
    //for modal ends

    constructor(
        private pendingConsigneeScreen: DashboardService,
        //for modal starts
        public dialog: MatDialog,
        private route: ActivatedRoute,
        private router: Router
        //for modal ends        
    ) {
        //for modal starts
        this.routeQueryParams$ = route.queryParams.subscribe( params => {
            if ( params['dialog'] ) {
                this.editDialog();
            }
        } );
        //for modal ends

    }

    ngOnInit(): void {

        //      the fifteen datatable starts
        this.dtOptionsPendingConsignee = {
            //};
            //columns is used to for export and others starts


            // the below code is for button export starts
            dom: 'Bfrtip',
            /*buttons: [
                      'excel', 'print'
                  ],*/
            buttons: [
                //                {
                //                    extend: 'excel',
                //                    text: '<i class="fas fa-file-excel"> Excel</i>',
                //                    titleAttr: 'Excel',
                //                    exportOptions: {
                //                        columns: ':visible'
                //                    }
                //                },
                //                {
                //                    extend: 'print',
                //                    text: '<i class="fas fa-print"> Print</i>',
                //                    titleAttr: 'Print',
                //                    //                                exportOptions: {
                //                    //                                    columns: ':visible'
                //                    //                                }
                //                }
            ],
            // the below code is for button export ends
            //place holder in search/filter in datatable starts
            language: {
                search: "_INPUT_",
                searchPlaceholder: "Search..."
            },
            //for search/filter in datatable to hide starts
            searching: false,
            //for search/filter in datatable to hide ends
            //place holder in search/filter in datatable ends
            //pagination in datatable starts
            pagingType: 'full_numbers',
            pageLength: 7,
            //pagination in datatable ends
            processing: true,
            //scroll in datatable starts
            "scrollX": true,
            "scrollCollapse": true,
            //scroll in datatable ends
        }
        //      the fifteen datatable ends
        //fifteen service starts 
        this.getDataPendingConsigneeDataTable = this.pendingConsigneeScreen.getSupplierMasterData()
        this.onDestroyUnsubscribtionPendingConsignee = this.getDataPendingConsigneeDataTable.subscribe( data => {
            this.pendingConsigneeDataList = data['data'];
            this.dtTriggerPendingConsignee.next();
        } );
        //fifteen service ends
    }

    ngOnDestroy(): void {
        this.dtTriggerPendingConsignee.unsubscribe();

        this.onDestroyUnsubscribtionPendingConsignee.unsubscribe();

        //for modal starts
        this.routeQueryParams$.unsubscribe();
        //for modal ends
    }
    //for modal starts
    editDialog(): void {
        const dialogRef = this.dialog.open( EditConsigneeDetailsComponent, {

            data: { name: this.name, animal: this.animal }
        } );

        dialogRef.afterClosed().subscribe( result => {
            console.log( 'The dialog was closed' );
            this.animal = result;
            this.router.navigate( ['.'], { relativeTo: this.route } );
        } );
    }
    //for modal ends
}
