@Component( {
    selector: 'app-agent-ranking',
    templateUrl: './agent-ranking.component.html',
    styleUrls: ['./agent-ranking.component.css']
} )
export class AgentRankingComponent implements OnDestroy, OnInit {

    getDataAgentRankingDataTable: any;

    agentRankingDataList: any;

    onDestroyUnsubscribtionAgentRanking: Subscription;
    @ViewChildren( DataTableDirective ) public dtElements: QueryList<DataTableDirective>;

    dtTriggerAgentRanking: Subject<any> = new Subject();

    dataTable: any;

    dtOptionsAgentRanking: any;

    constructor( private agentRankingScreen: DashboardService,
          //for redirect to other page starts
            private router: Router
          //for redirect to other page ends
        ) {


    }

    ngOnInit(): void {
        //  the nine datatable starts
        this.dtOptionsAgentRanking = {
            //};
            //columns is used to for export and others starts
            columns: [

                {
                    title: 'Agent Name',
                    data: 'name'
                },
                {
                    title: 'Act Wt',
                    data: 'symbol'
                },
                {
                    title: 'Chg Wt',
                    data: 'grade'
                },
                {
                    title: 'Amount Payable',
                    data: 'position'
                }
            ],

            // the below code is for button export starts
            dom: 'Bfrtip',
            /*buttons: [
                      'excel', 'print'
                  ],*/
            buttons: [

            ],
            // the below code is for button export ends
            //place holder in search/filter in datatable starts
            language: {
                search: "_INPUT_",
                searchPlaceholder: "Search..."
            },
            //place holder in search/filter in datatable ends
            //pagination in datatable starts
            pagingType: 'full_numbers',
            pageLength: 5,
            //pagination in datatable ends
            processing: true,
            //scroll in datatable starts
            "scrollX": true,
            "scrollCollapse": true,
            //scroll in datatable ends
        }
        //the nine datatable ends
        //        //nine service starts 
        this.getDataAgentRankingDataTable = this.agentRankingScreen.getSupplierMasterData()
        this.onDestroyUnsubscribtionAgentRanking = this.getDataAgentRankingDataTable.subscribe( data => {
            this.agentRankingDataList = data['data'];
            this.dtTriggerAgentRanking.next();
        } );
        //        //nine service ends
    }


    ngOnDestroy(): void {

        this.dtTriggerAgentRanking.unsubscribe();

        this.onDestroyUnsubscribtionAgentRanking.unsubscribe();
    }


  //for redirect to other page starts
    goToRankingPage() {
        this.router.navigate( ['/report/agents/ranking'] );
    }
  //for redirect to other page ends
}

