import { Component, OnInit, ViewChildren, QueryList, OnDestroy } from '@angular/core';
import { NgModule, ViewChild } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
//for datatable starts

import { DataTableDirective } from "angular-datatables";
//from the particular local folder starts
//import { LrDispatchBookingReportService } from './lr-dispatch-booking-report-service';
//from the particular local folder ends
//from the particular global folder starts
//import { SuplierService } from './supplier-master-service';
import { DashboardService } from 'src/app/dataService/dashboard-service';
//from the particular global folder ends
import { ElementRef } from "@angular/core";
import { Subject, Subscription } from 'rxjs';

//for datatable ends

@Component( {
    selector: 'app-source-stocks-summary',
    templateUrl: './source-stocks-summary.component.html',
    styleUrls: ['./source-stocks-summary.component.css']
} )
export class SourceStocksSummaryComponent implements OnDestroy, OnInit {

    getDataSourceStocksSummaryDataTable: any;

    sourceStocksSummaryDataList: any;

    onDestroyUnsubscribtionSourceStocksSummary: Subscription;

    @ViewChildren( DataTableDirective ) public dtElements: QueryList<DataTableDirective>;

    dtTriggerSourceStocksSummary: Subject<any> = new Subject();

    dataTable: any;

    dtOptionsSourceStocksSummary: any;


    constructor( private sourceStocksSummaryScreen: DashboardService ) {


    }

    ngOnInit(): void {
        //  the seven datatable starts
        this.dtOptionsSourceStocksSummary = {
            //};
            //columns is used to for export and others starts
            columns: [

                {
                    title: 'Source',
                    data: 'name'
                },
                {
                    title: 'Act Wt',
                    data: 'symbol'
                },
                {
                    title: 'Chg Wt',
                    data: 'grade'
                }
            ],

            // the below code is for button export starts
            dom: 'Bfrtip',
            /*buttons: [
                      'excel', 'print'
                  ],*/
            buttons: [

            ],
            // the below code is for button export ends
            //place holder in search/filter in datatable starts
            language: {
                search: "_INPUT_",
                searchPlaceholder: "Search..."
            },
            //for search/filter in datatable to hide starts
            searching: false,
            //for search/filter in datatable to hide ends
            //place holder in search/filter in datatable ends
            //pagination in datatable starts
            pagingType: 'full_numbers',
            pageLength: 7,
            //pagination in datatable ends
            processing: true,
            //scroll in datatable starts
            "scrollX": true,
            "scrollCollapse": true,
            //scroll in datatable ends
        }
        //      the seven datatable ends

        //seven service starts 
        this.getDataSourceStocksSummaryDataTable = this.sourceStocksSummaryScreen.getSupplierMasterData()
        this.onDestroyUnsubscribtionSourceStocksSummary = this.getDataSourceStocksSummaryDataTable.subscribe( data => {
            this.sourceStocksSummaryDataList = data['data'];
            this.dtTriggerSourceStocksSummary.next();
        } );
        //seven service ends
    }

    ngOnDestroy(): void {
        this.dtTriggerSourceStocksSummary.unsubscribe();

        this.onDestroyUnsubscribtionSourceStocksSummary.unsubscribe();
    }

}
