import { Component, OnInit, ViewChildren, QueryList, OnDestroy } from '@angular/core';
import { NgModule, ViewChild } from '@angular/core';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
//for datatable starts

import { DataTableDirective } from "angular-datatables";
//from the particular local folder starts
//import { LrDispatchBookingReportService } from './lr-dispatch-booking-report-service';
//from the particular local folder ends
//from the particular global folder starts
//import { SuplierService } from './supplier-master-service';
import { DashboardService } from 'src/app/dataService/dashboard-service';
//from the particular global folder ends
import { ElementRef } from "@angular/core";
import { Subject, Subscription } from 'rxjs';

//for datatable ends
//for popup modal starts 
import { NgbModal, ModalDismissReasons, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
//for popup modal ends

@Component( {
    selector: 'app-pending-stocks-sms',
    templateUrl: './pending-stocks-sms.component.html',
    styleUrls: ['./pending-stocks-sms.component.css']
} )
export class PendingStocksSmsComponent implements OnInit {
    //for popup modal starts
    closeResultMobile: string;
    closeResultContent: string;
    closeResultSuccess: string;
    //for popup modal ends
    getDataPendingStocksSmsDataTable: any;

    pendingStocksSmsDataList: any;

    onDestroyUnsubscribtionPendingStocksSms: Subscription;

    @ViewChildren( DataTableDirective ) public dtElements: QueryList<DataTableDirective>;

    dtTriggerPendingStocksSms: Subject<any> = new Subject();

    dataTable: any;

    dtOptionsPendingStocksSms: any;

    constructor( private pendingStocksSmsScreen: DashboardService, private modalService: NgbModal ) {


    }

    ngOnInit(): void {

        //      the fifteen datatable starts
        this.dtOptionsPendingStocksSms = {
            //};
            //columns is used to for export and others starts


            // the below code is for button export starts
            dom: 'Bfrtip',
            /*buttons: [
                      'excel', 'print'
                  ],*/
            buttons: [
                //                {
                //                    extend: 'excel',
                //                    text: '<i class="fas fa-file-excel"> Excel</i>',
                //                    titleAttr: 'Excel',
                //                    exportOptions: {
                //                        columns: ':visible'
                //                    }
                //                },
                //                {
                //                    extend: 'print',
                //                    text: '<i class="fas fa-print"> Print</i>',
                //                    titleAttr: 'Print',
                //                    //                                exportOptions: {
                //                    //                                    columns: ':visible'
                //                    //                                }
                //                }
            ],
            // the below code is for button export ends
            //place holder in search/filter in datatable starts
            language: {
                search: "_INPUT_",
                searchPlaceholder: "Search..."
            },
            //for search/filter in datatable to hide starts
            searching: false,
            //for search/filter in datatable to hide ends
            //place holder in search/filter in datatable ends
            //pagination in datatable starts
            pagingType: 'full_numbers',
            pageLength: 6,
            //pagination in datatable ends
            processing: true,
            //scroll in datatable starts
            "scrollX": true,
            "scrollCollapse": true,
            //scroll in datatable ends
        }
        //      the fifteen datatable ends
        //fifteen service starts 
        this.getDataPendingStocksSmsDataTable = this.pendingStocksSmsScreen.getSupplierMasterData()
        this.onDestroyUnsubscribtionPendingStocksSms = this.getDataPendingStocksSmsDataTable.subscribe( data => {
            this.pendingStocksSmsDataList = data['data'];
            this.dtTriggerPendingStocksSms.next();
        } );
        //fifteen service ends
    }

    ngOnDestroy(): void {
        this.dtTriggerPendingStocksSms.unsubscribe();

        this.onDestroyUnsubscribtionPendingStocksSms.unsubscribe();
    }

    //for popup modal starts 
    openMobile( contentMobile ) {
        this.modalService.open( contentMobile, { centered: true } ).result.then(
            resultMobile => {
                this.closeResultMobile = `Closed with: ${resultMobile}`;
            },
            reason => {
                this.closeResultMobile = `Dismissed ${this.getDismissReason( reason )}`;
            }
        );
    }

    openPopup( content ) {
        this.modalService.open( content, { centered: true } ).result.then(
            resultContent => {
                this.closeResultContent = `Closed with: ${resultContent}`;
            },
            reason => {
                this.closeResultContent = `Dismissed ${this.getDismissReason( reason )}`;
            }
        );
    }

    openModalSuccess( contentSuccess ) {
        this.modalService.open( contentSuccess, { centered: true } ).result.then(
            resultSuccess => {
                this.closeResultSuccess = `Closed with: ${resultSuccess}`;
            },
            reason => {
                this.closeResultSuccess = `Dismissed ${this.getDismissReason( reason )}`;
            }
        );
    }
    private getDismissReason( reason: any ): string {
        if ( reason === ModalDismissReasons.ESC ) {
            return 'by pressing ESC';
        } else if ( reason === ModalDismissReasons.BACKDROP_CLICK ) {
            return 'by clicking on a backdrop';
        } else {
            return `with: ${reason}`;
        }
    }

    //for popup modal ends

}
