	//for popup modal ends
	//for datatable starts
	gettingDataFrmServiceFrPendingLrDetailsTable: any;


	scheduleTripDeliverySummaryDataList: any;
	validationForUnCheckLrs: any;


	onDestroyUnsubscribtionPendingLrDetails: Subscription;

	//for datatable ends

	//summaryTable:any;

	loadingIndicator = true;

	//for datatable starts
	@ViewChildren(DataTableDirective) public dtElements: QueryList<DataTableDirective>;

	dtTriggerScheduleTripDelivery: Subject<any> = new Subject();

	dataTable: any;
	dtOptionsScheduleTripDelivery: any;

	//for datePicker starts
	model: NgbDateStruct;
	//for datepicker ends


	isLoggedIn = true;
	userDataDtoReturnSession: any;
	showSpinnerForAction = false;
	showSpinnerForActionReasonPopup = false;
	address: any;
	viewType = '';

