@Component( {
    selector: 'app-fortnight-statement-summary',
    templateUrl: './fortnight-statement-summary.component.html',
    styleUrls: ['./fortnight-statement-summary.component.css']
} )
export class FortnightStatementSummaryComponent implements OnDestroy, OnInit {

    getDataFortnightStatementSmryDataTable: any;

    fortnightStatementSmryDataList: any;

    onDestroyUnsubscribtionFortnightStatementSmry: Subscription;
    @ViewChildren( DataTableDirective ) public dtElements: QueryList<DataTableDirective>;
    dtTriggerFortnightStatementSmry: Subject<any> = new Subject();
    dataTable: any;


    dtOptionsFortnightStatementSmry: any;

    constructor( private fortnightStatementSummaryScreen: DashboardService ) {


    }

    ngOnInit(): void {
        //    the two datatable starts
        this.dtOptionsFortnightStatementSmry = {
            //};
            //columns is used to for export and others starts
            columns: [

                {
                    title: 'Destination',
                    data: 'name'
                },
                {
                    title: 'Stmt Dt',
                    data: 'symbol'
                },
                {
                    title: 'Receivable Amt',
                    data: 'position'
                }
            ],

            // the below code is for button export starts
            dom: 'Bfrtip',
            /*buttons: [
                      'excel', 'print'
                  ],*/
            buttons: [

            ],
            // the below code is for button export ends
            //place holder in search/filter in datatable starts
            language: {
                search: "_INPUT_",
                searchPlaceholder: "Search..."
            },
            //for search/filter in datatable to hide starts
            searching: false,
            //for search/filter in datatable to hide ends
            //place holder in search/filter in datatable ends
            //pagination in datatable starts
            pagingType: 'full_numbers',
            pageLength: 7,
            //pagination in datatable ends
            processing: true,
            //scroll in datatable starts
            "scrollX": true,
            "scrollCollapse": true,
            //scroll in datatable ends
        }
        //      the two datatable ends

        //two service starts 
        this.getDataFortnightStatementSmryDataTable = this.fortnightStatementSummaryScreen.getSupplierMasterData()
        this.onDestroyUnsubscribtionFortnightStatementSmry = this.getDataFortnightStatementSmryDataTable.subscribe( data => {
            this.fortnightStatementSmryDataList = data['data'];
            this.dtTriggerFortnightStatementSmry.next();
        } );
        //two service ends


    }
    ngOnDestroy(): void {
        this.dtTriggerFortnightStatementSmry.unsubscribe();

        this.onDestroyUnsubscribtionFortnightStatementSmry.unsubscribe();
    }
}
